import React from 'react';
import { Link } from 'react-router-dom';
import './EnterButton.scss';

const EnterButton = (props)=>{
  return(
    <Link to={props.linck} className="rout-linck enter-button">
      <button className="email__input email__input_button">{props.text}</button>
    </Link>
  );
}

export default EnterButton;



