import React from 'react';
import './UserTest.scss';

const UserTest = (props)=>{
  return(
    <div className="user-test">
      
      <div className="user-test__box">
        <div className="user-test__title">
          <span>2. Алелгические реакции <br/>на лекарства</span>
        </div>
        <div className="user-test__yes-no">
          <button className="_active">Есть</button>
          <button >Нет</button>
        </div>
        <div className="user-test__options">
          <span className="user-test__options-title">Название препарата</span>
          <ul className="user-test__options__list">
            <li>
              <span className="user-test__options-text">Супрадин</span>
              <span className="user-test__options-close">+</span>
            </li>
            <li>
              <span className="user-test__options-text">Алкозельцер</span>
              <span className="user-test__options-close">+</span>
            </li>
          </ul>
          <div className="user-test__add-option">
            <div>
              <span className="-add">+</span>
              <span className="-text">Добавить еще препарат</span>
            </div>
          </div>
        </div>
      </div>
      
    </div>
  )
};

export default UserTest;