import React from 'react';
import { Link } from 'react-router-dom';
import './RecoverPassvord.scss';
import BackButton from '../BackButton/BackButton';
import AppHeader from '../AppHeader/AppHeader';

const RecoverPassvord = (props)=>{
  return(
    <div className="recover-box">
    <BackButton linck="comein"/>
     <AppHeader text="Восстановление пароля"/>
     <div className="recover_form">
      <div className="recover_form-title">
        <span>Введите ваш емейл и мы <br/>вышлем вам новый пароль</span>
      </div>
      <form action="">
        <input type="text" placeholder="E-mail" className="email__input"/>
      </form>
     </div>
     <Link to="/recoverpass1" className="rout-linck">
      <button className="email__input email__input_button">Отправить</button>
     </Link>
     
    </div>
  )
};

export default RecoverPassvord;



