import React from 'react';
import { Link } from 'react-router-dom';

const ComeIn = (props)=>{

  // В зависимости от экрана на который можно перейти по кнопке меняем текст
  
  let buttonText = {
    comein: [ 'Уже есть аккаунт? ', 'Войдите.' ],
    recoverpass: ['Забыли пароль? ', 'Восстановить.']
  };
  // console.log(props);

  // console.log(props.linck);
  // props.linck = (props.linck !==undefined)? props.linck:'#';
  return(
    <Link to={props.linck} className="authorization__message">
      <div>
        <span className="authorization__message-text">{buttonText[props.linck][0]}</span> 
        <span className="authorization__message-action">{buttonText[props.linck][1]}</span>
      </div>
    </Link>
  );
}
export default ComeIn;