import React, { Component } from 'react';
import 'normalize.css';
import './App.scss';
import { BrowserRouter, Route } from 'react-router-dom';
import ViewAppStart from './views/ViewAppStart.jsx';
import ViewRegistration from './views/ViewRegistration.jsx';
import ViewComeIn from './views/ViewComeIn.jsx';
import ViewRecoverPassword from './views/ViewRecoverPassword.jsx';
import ViewRecoverPasswordStep1 from './views/ViewRecoverPasswordStep1.jsx';
import ViewRecoverPasswordStep2 from './views/ViewRecoverPasswordStep2.jsx';
import ViewRecoverPasswordStep3 from './views/ViewRecoverPasswordStep3.jsx';
import ViewProfileStep1 from './views/ViewProfileStep1.jsx';
import ViewProfileStep2 from './views/ViewProfileStep2.jsx';

class App extends Component {

  render() {
    return (
      <BrowserRouter>
        <div className="app-wrap">
          <Route exact path="/" component={ViewAppStart}/>
          <Route path="/registration" component={ViewRegistration}/>
          <Route path="/comein" component={ViewComeIn}/>
          <Route path="/recoverpass" component={ViewRecoverPassword}/>
          <Route path="/recoverpass1" component={ViewRecoverPasswordStep1}/>
          <Route path="/recoverpass2" component={ViewRecoverPasswordStep2}/>
          <Route path="/recoverpass3" component={ViewRecoverPasswordStep3}/>
          <Route path="/profile1" component={ViewProfileStep1}/>
          <Route path="/profile2" component={ViewProfileStep2}/>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
