import React, { Component } from 'react';
import i_warned from './img/i_danger.svg';

class Form extends Component{
  constructor(props) {
    super(props);

    this.state = {
      className: "email__form",
      classNameInput: "input__box"
    };
    this.inputChange = this.inputChange.bind(this);
  }
  inputChange(evnt){
    if(evnt.target.value === ''){
      this.setState({
        className: "email__form",
        classNameInput: "input__box"
      });
    }else{
      this.setState({
        className: "email__form email__form_error",
        classNameInput: "input__box input__box_error"
      });
    }
  }
  buttonClick(evnt){
    evnt.preventDefault();
  }
  render(){
    return(
      <div className="email">
        <h3 className="email__title">Или с помощью вашего e-mail</h3>
        {/* email__form_error если естьь этот клас то видно сообщение с ошибкой само сообщени будем менять*/}
        <form className={this.state.className} action="">
          <div className={this.state.classNameInput}>
            <input className="email__input email__input_email" type="text" placeholder="E-mail" onChange={this.inputChange}/>
            <div className="input__message  input__message_mail">
              <span className="input__message-text">{this.props.errorMessage}</span>
              <span className="input__message-icon">
                <img src={i_warned} alt="i_warned"/>
              </span>
            </div>
          </div>
          <div className="input__box input__box_pass">
            <input className="email__input email__input_password" type="text" placeholder="Пароль"/>
            <div className="input__message input__message_final">
              <span className="input__message-text">Емейл или пароль не верный</span>
              <span className="input__message-icon">
                <img src={i_warned} alt="i_warned"/>
              </span>
            </div>
          </div>
          <button className="email__input email__input_button" type="submit" onClick={this.buttonClick}>Создать аккаунт</button>
        </form>
      </div>
    );
  }
}
export default Form;