import React, {Component} from 'react';

class UserGender extends Component{
  constructor(props){
    super(props)
    this.state = {
      classNameMan: "_active",
      classNameWomen: ''
    }
  }
  onChange(event){
    event.preventDefault();
    alert('Не делай этого!!!')
  }
  render(){
    return(
      <div className="user-gender__select">
        <span className="user-gender__label">Пол</span>
        <div className="user-gender__box">
          <button className={this.state.classNameMan} onClick={this.onChange}>Мужской</button>
          <button className={this.state.classNameWomen} onClick={this.onChange}>Женский</button>
        </div>
      </div>
    )
  }
};

export default UserGender;