import React from 'react';
import { Link } from 'react-router-dom';
import './UserInfo.scss';
import userPhoto from '../../assets/img/profile_photo.png';
import UserInput from './UserInput';
import UserGender from './UserGender';
import i_camera from './i_camera.svg'

const UserInfo = (props)=>{
  return(
    <div className="user-info">
      <div className="user-info__photo">
        <div className="user-info__photo-box user-info__photo-box_white">
          {/* <img src={userPhoto} alt="userPhoto"/> */}
          <label htmlFor="load-foto" className="user-info__photo-load">
            <img src={i_camera} alt="i_camera"/>
            <span>Загрузить фото</span>
            <input id="load-foto" type="file"/>
          </label>
        </div>
        
      </div>
      <form className="user-info__form">
        <UserInput idInput="name" label="Имя"/>
        <UserInput idInput="fName" label="Фамилия"/>
        <UserInput idInput="lName" label="Отчество"/>
        <UserInput idInput="bDate" label="Дата рождения"/>
        <UserGender />
      </form>
    </div>
  )
};

export default UserInfo;