import React from 'react';
import './MailSent.scss';
import BackButton from '../BackButton/BackButton';
import AppHeader from '../AppHeader/AppHeader';
import i_letter from './i_letter.svg';

const MailSent = (props)=>{
  return(
    <div className="mail-sent">
      <BackButton linck="/recoverpass"/>
      <AppHeader text="Восстановление пароля"/>
      <div className="message">
        <div className="message__image">
        < img src={i_letter} alt="i_letter"/>
        </div>
        <div className="message__text">
          <span>Отправили пароль вам <br/>на e-mail 1212212@gmail.com</span>
        </div>
      </div>
    </div>
  )
};

export default MailSent;
