import React from 'react';
import { Link } from 'react-router-dom';
import './Succes.scss';
import check from './check.svg';

const Succes = (props)=>{
  return(
    <div className="change-succes">
      
      <div className="change-succes__box">
        <div className="change-succes__cirle">
          <div>
          <img src={check} alt=""/>
          </div>
        </div>
        <div className="change-succes__text">
          <span>Поздравляем! <br/> {props.text}</span>
        </div>
      </div>
      <Link to={props.linck} className="rout-linck">
        <button className="email__input email__input_button">{props.buttonText}</button>
      </Link>
     
    </div>
  )
};

export default Succes;