import React from 'react';
import { Link } from 'react-router-dom';
import MailSent from '../components/MailSent/MailSent';

const ViewRecoverPasswordStep1 = (props)=>{
  return(
    <div className="app app_dark-bg">
      <div className="app-view">
        <div className="app-box">
          <MailSent />
          <Link to="/recoverpass2" className="nextView">Next</Link>
        </div>
      </div>
    </div>
  );
}

export default ViewRecoverPasswordStep1;