import React from 'react';
import logo from './logo.svg';
import './Logo.scss';

const Logo = (props)=>{
  return(
    <div className="logo-box">
      <div className="logo">
        <div className="logo__image">
          <span>
            <img src={logo} alt="logo"/>
          </span>s
        </div>
        <h1 className="logo__text">ASpine</h1>
      </div>
    </div>
  )
};

export default Logo;



