import React from 'react';
import i_fb from './img/i_fb.svg';
import i_vk from './img/i_vk.svg';

const Social = (props)=>{
  return(
    <div className="social">
      <ul className="social__list">
        <li className="social__item social__item_fb">
          <span className="social__icon">
            <img src={i_fb} alt="social__icon"/>
          </span>
          <span className="social__text">
            Создать аккаунт с Facebook
          </span>
        </li>
        <li className="social__item social__item_vk">
          <span className="social__icon">
            <img src={i_vk} alt="social__icon"/>
          </span>
          <span className="social__text">
            Создать аккаунт с Vkontakte
          </span>
        </li>
      </ul>
    </div>
  );
}

export default Social;